function isNull(value) {
  return value === '' || value === null || value === undefined;
}

function isNullOREmpty(value) {
  return isNull(value) || value === ' ';
}

function getUUID(length) {
  let len = length || 9;
  let chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';
  let maxPos = chars.length;
  let id = '';
  for (let i = 0; i < len; i++) {
    id += chars.charAt(Math.floor(Math.random() * maxPos));
  }
  return id;
}

//随机颜色
function gradientColor(startColor, endColor, step) {
  let startRGB = this.colorRgb(startColor); //转换为rgb数组模式
  let startR = startRGB[0];
  let startG = startRGB[1];
  let startB = startRGB[2];

  let endRGB = this.colorRgb(endColor);
  let endR = endRGB[0];
  let endG = endRGB[1];
  let endB = endRGB[2];

  let sR = (endR - startR) / step; //总差值
  let sG = (endG - startG) / step;
  let sB = (endB - startB) / step;

  let colorArr = [];
  for (let i = 0; i < step; i++) {
    //计算每一步的hex值
    let hex = this.colorHex(
      'rgb(' +
        parseInt(sR * i + startR) +
        ',' +
        parseInt(sG * i + startG) +
        ',' +
        parseInt(sB * i + startB) +
        ')'
    );
    colorArr.push(hex);
  }
  return colorArr;
}

// 将hex表示方式转换为rgb表示方式(这里返回rgb数组模式)
gradientColor.prototype.colorRgb = function (sColor) {
  let reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
  sColor = sColor.toLowerCase();
  if (sColor && reg.test(sColor)) {
    if (sColor.length === 4) {
      let sColorNew = '#';
      for (let i = 1; i < 4; i += 1) {
        sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
      }
      sColor = sColorNew;
    }
    //处理六位的颜色值
    let sColorChange = [];
    for (let i = 1; i < 7; i += 2) {
      sColorChange.push(parseInt('0x' + sColor.slice(i, i + 2)));
    }
    return sColorChange;
  } else {
    return sColor;
  }
};
// 将rgb表示方式转换为hex表示方式
gradientColor.prototype.colorHex = function (rgb) {
  let _this = rgb;
  let reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
  if (/^(rgb|RGB)/.test(_this)) {
    let aColor = _this.replace(/(?:\(|\)|rgb|RGB)*/g, '').split(',');
    let strHex = '#';
    for (let i = 0; i < aColor.length; i++) {
      let hex = Number(aColor[i]).toString(16);
      hex = hex < 10 ? 0 + '' + hex : hex; // 保证每个rgb的值为2位
      if (hex === '0') {
        hex += hex;
      }
      strHex += hex;
    }
    if (strHex.length !== 7) {
      strHex = _this;
    }
    return strHex;
  } else if (reg.test(_this)) {
    let aNum = _this.replace(/#/, '').split('');
    if (aNum.length === 6) {
      return _this;
    } else if (aNum.length === 3) {
      let numHex = '#';
      for (let i = 0; i < aNum.length; i += 1) {
        numHex += aNum[i] + aNum[i];
      }
      return numHex;
    }
  } else {
    return _this;
  }
};

function getRoundNumber(num, x) {
  if (isNull(x)) x = 0;
  return Math.round(num * Math.pow(10, x)) / Math.pow(10, x);
}

// 是否是中文
function isChinese(temp) {
  var re = /.*[\u4e00-\u9fa5]+.*$/;
  if (re.test(temp)) return true;
  return false;
}

function chunk(array, size) {
  // 分割数据 (数组，切割的个数)
  let length = array == null ? 0 : array.length;
  if (!length || size < 1) {
    return [];
  }
  let index = 0,
    resIndex = 0,
    result = Array(Math.ceil(length / size));
  while (index < length) {
    result[resIndex++] = array.slice(index, (index += size));
  }
  return result;
}
//列表时间戳格式化
function timeStampFomart(timestamp) {
  let date = new Date(timestamp);
  let Y = date.getFullYear() + '-';
  let M =
    (date.getMonth() + 1 < 10
      ? '0' + (date.getMonth() + 1)
      : date.getMonth() + 1) + '-';
  let D = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
  let h = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
  let m = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
  let s = date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds();
  return Y + M + D + ' ' + h + ':' + m + ':' + s;
}
//url地址解析成对象
function parseQueryString(url) {
  var reg_url = /^[^\?]+\?([\w\W]+)$/,
    reg_para = /([^&=]+)=([\w\W]*?)(&|$|#)/g,
    arr_url = reg_url.exec(url),
    ret = {};
  if (arr_url && arr_url[1]) {
    var str_para = arr_url[1],
      result;
    while ((result = reg_para.exec(str_para)) != null) {
      ret[result[1]] = result[2];
    }
  }
  return ret;
}
//获取url某一个参数的值
function getQueryString(name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
  var r = window.location.search.substr(1).match(reg);
  if (r != null) return unescape(r[2]);
  return null;
}

//将bbox的字符串成浮点型的数组
function parseBbox(bbox) {
  let bs = bbox.split(',');
  return [
    parseFloat(bs[0]),
    parseFloat(bs[1]),
    parseFloat(bs[2]),
    parseFloat(bs[3])
  ];
}

//深拷贝
function deepClone(obj) {
  let objTemp = JSON.stringify(obj);
  let object = JSON.parse(objTemp);
  return object;
}

//echart自定义dataview
function customDataView(opt, type) {
  let axisData = type == 0 ? opt.xAxis[0].data : opt.yAxis[0].data; //坐标数据
  let series = opt.series; //折线图数据
  if (type == 1) {
    series = [];
    series.push(opt.series[0]);
  }
  let tdHeads = '<td  style="padding: 4px 10px;">时间</td>'; //表头
  let tdBodys = ''; //数据
  series.forEach(function (item) {
    tdHeads += `<td style="padding: 4px 10px;height:100%">${item.name}</td>`;
  });
  let table = `<table border="1" style="margin: 0 1%;border-collapse:collapse;font-size:15px;text-align:center; color: #444;width:98%"><tbody><tr>${tdHeads} </tr>`;
  for (let i = 0, l = axisData.length; i < l; i++) {
    for (let j = 0; j < series.length; j++) {
      tdBodys += `<td>${series[j].data[i].value}</td>`;
    }
    table += `<tr><td style="padding:4px 10px">${axisData[i]}</td>${tdBodys}</tr>`;
    tdBodys = '';
  }
  table += '</tbody></table>';
  return table;
}

//排序
function sortData(rawdata) {
  let tempData = deepClone(rawdata);
  let count = tempData.value.length;
  while (count > 0) {
    for (let idx = 0; idx < tempData.value.length; idx++) {
      let tempName = '';
      let tempCode = '';
      let tempVal = 0;
      if (Number(tempData.value[idx]) < Number(tempData.value[idx + 1])) {
        tempName = tempData.name[idx];
        tempCode = tempData.code[idx];
        tempVal = tempData.value[idx];
        tempData.name[idx] = tempData.name[idx + 1];
        tempData.code[idx] = tempData.code[idx + 1];
        tempData.value[idx] = tempData.value[idx + 1];
        tempData.name[idx + 1] = tempName;
        tempData.code[idx + 1] = tempCode;
        tempData.value[idx + 1] = tempVal;
      }
    }
    count--;
  }
  return tempData;
}

//取数据中前几项
function cutData(rawdata, lastL) {
  let tempdata = deepClone(rawdata);
  if (tempdata.value.length > lastL) {
    let cutL = tempdata.value.length - lastL;
    for (let i in tempdata) {
      tempdata[i].splice(lastL - 1, cutL);
    }
  }
  return tempdata;
}

//将字典表对象数组变成map,array:字典表对象数组
function changeObjToMap(array) {
  var map = new Map();
  array.forEach((obj) => {
    map.set(obj.code, obj.data);
  });
  return map;
}
