/**
 * 快速排序
 */
function quicksort(array) {
  let sort = (arr, left = 0, right = arr.length - 1) => {
    if (left >= right) {
      return;
    }
    let i = left;
    let j = right;
    const baseVal = arr[j];
    while (i < j) {
      while (i < j && arr[i] <= baseVal) {
        i++;
      }
      arr[j] = arr[i];
      while (j > i && arr[j] >= baseVal) {
        j--;
      }
      arr[i] = arr[j];
    }
    arr[j] = baseVal;
    sort(arr, left, j - 1);
    sort(arr, j + 1, right);
  };
  const newArr = array.concat();
  sort(newArr);
  return newArr;
}
//时间戳格式化为日期
function dataStampFomart(timestamp) {
  let date = new Date(timestamp);
  let Y = date.getFullYear() + '-';
  let M =
    (date.getMonth() + 1 < 10
      ? '0' + (date.getMonth() + 1)
      : date.getMonth() + 1) + '-';
  let D = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
  return Y + M + D;
}
//计算百分比
function GetPercent(num, total) {
  num = parseFloat(num);
  total = parseFloat(total);
  if (isNaN(num) || isNaN(total)) {
    return '-';
  }
  return total <= 0 ? '0%' : Math.round((num / total) * 10000) / 100.0 + '%';
}

function formatFileSize(fileSize) {
  if (fileSize < 1024) {
    return fileSize + 'B';
  } else if (fileSize < 1024 * 1024) {
    var temp = fileSize / 1024;
    temp = temp.toFixed(2);
    return temp + 'KB';
  } else if (fileSize < 1024 * 1024 * 1024) {
    var temp = fileSize / (1024 * 1024);
    temp = temp.toFixed(2);
    return temp + 'MB';
  } else {
    var temp = fileSize / (1024 * 1024 * 1024);
    temp = temp.toFixed(2);
    return temp + 'GB';
  }
}

function getPageInfo(page = 1, pageSize = 15, totalData = []) {
  const { length } = totalData;
  const tableData = {
    data: [],
    page,
    pageSize,
    length,
  };
  if (pageSize >= length) {
    //pageSize大于等于总数据长度，说明只有1页数据或没有数据
    tableData.data = totalData;
    tableData.page = 1; //直接取第一页
  } else {
    //pageSize小于总数据长度，数据多余1页
    const num = pageSize * (page - 1); //计算当前页（不含）之前的所有数据总条数
    if (num < length) {
      //如果当前页之前所有数据总条数小于（不能等于）总的数据集长度，则说明当前页码没有超出最大页码
      const startIndex = num; //当前页第一条数据在总数据集中的索引
      const endIndex = num + pageSize - 1; //当前页最后一条数据索引
      tableData.data = totalData.filter(
        (_, index) => index >= startIndex && index <= endIndex
      ); //当前页数据条数小于每页最大条数时，也按最大条数范围筛取数据
    } else {
      //当前页码超出最大页码，则计算实际最后一页的page，自动返回最后一页数据
      const size = parseInt(length / pageSize); //取商
      const rest = length % pageSize; //取余数
      if (rest > 0) {
        //余数大于0，说明实际最后一页数据不足pageSize，应该取size+1为最后一条的页码
        tableData.page = size + 1; //当前页码重置，取size+1
        tableData.data = totalData.filter(
          (_, index) => index >= pageSize * size && index <= length
        );
      } else if (rest === 0) {
        //余数等于0，最后一页数据条数正好是pageSize
        tableData.page = size; //当前页码重置，取size
        tableData.data = totalData.filter(
          (_, index) => index >= pageSize * (size - 1) && index <= length
        );
      } //注：余数不可能小于0
    }
  }
  return tableData;
}
