import Vue from 'vue';
import { sync } from 'vuex-router-sync';
import Kcore from '@ktw/kcore';
import '@ktw/kcore/dist/styles/kcore.css';
import KMap2d from '@ktw/kmap2d';
import '@ktw/kmap2d/dist/kmap2d.css';
import filter from '@/filter';
import directive from '@/directive';
import store from '@/store';
import router from '@/router';
import App from './App';
import '@/settings';
import '@/components';
import '@/config';
import '@/utils/api';
import '@/styles/index.less';
Vue.config.devtools = true;
Vue.config.productionTip = false;
Vue.use(Kcore, {
  store,
});
sync(store, router);
Vue.use(KMap2d);
// 全局注册filter
Object.keys(filter).forEach((key) => {
  Vue.filter(key, filter[key]);
});
// 全局注册directive
Object.keys(directive).forEach((key) => {
  Vue.directive(key, directive[key]);
});

// 处理svg模块依赖
// 详情见： https://doc.webpack-china.org/guides/dependency-management/#require-context
const requireAll = (requireContext = {}) =>
  requireContext.keys().map(requireContext);
const req = require.context('./assets/icon', true, /\.svg$/);
requireAll(req);

/* eslint-disable no-new */
window.vueApp = new Vue({
  el: '#app',
  router,
  store,
  components: {
    App,
  },
  template: '<App/>',
});

Object.defineProperty(Vue.prototype, '$events', {
  get() {
    return window.vueApp;
  },
});
