<script>
import vueSeamless from 'vue-seamless-scroll';
import { isNull } from '../utils/commom';
import appConfig from 'appConfig';
import * as echarts from 'echarts';
// import { getFilterBySql } from '@/utils/getFilterBySql.js';

let bgColor = [
  ['rgba(0, 229, 255, 0)', 'rgba(0, 229, 255, 0.32)'],
  ['rgba(208, 44, 255, 0)', 'rgba(208, 44, 255, 0.32)'],
  ['rgba(255, 174, 0, 0)', 'rgba(255, 174, 0, 0.32)'],
  ['rgba(208, 44, 255, 0)', 'rgba(208, 44, 255, 0.32)'],
  ['rgba(34, 224, 115, 0)', 'rgba(34, 224, 115, 0.32)'],
  ['rgba(31, 105, 255, 0)', 'rgba(31, 105, 255, 0.32)'],
  ['rgba(0, 229, 255, 0)', 'rgba(0, 229, 255, 0.32)'],
  ['rgba(245, 210, 75, 0)', 'rgba(245, 210, 75, 0.32)'],
  ['rgba(10, 147, 246, 0)', 'rgba(10, 147, 246, 0.32)'],
  ['rgba(224, 134, 54, 0)', 'rgba(224, 134, 54, 0.32)'],
  ['rgba(0, 229, 255, 0)', 'rgba(0, 229, 255, 0.32)'],
  ['rgba(34, 224, 115, 0)', 'rgba(34, 224, 115, 0.32)'],
  ['rgba(255, 174, 0, 0)', 'rgba(255, 174, 0, 0.32)'],
  ['rgba(0, 229, 255, 0)', 'rgba(0, 229, 255, 0.32)'],
  ['rgba(31, 105, 255, 0)', 'rgba(31, 105, 255, 0.32)'],
  ['rgba(34, 224, 115, 0)', 'rgba(34, 224, 115, 0.32)']
];

export default {
  name: 'DataSharingSituationCharts', // 委办单位数据共享交换情况图表组件
  components: { vueSeamless },
  props: {
    options: {
      type: Object,
      default() {
        return null;
      },
    },
    selectXzqdm: {
      type: String,
      default() {
        return null;
      },
    },
  },
  data() {
    return {
      // configData: appConfig.app.totalDataInfo.SpatializationOfOperationalData,
      echartsData: [
        { name: '公安局', unit: '次', value: 65, typeName: '' },
        { name: '交通局', unit: '次', value: 6, typeName: '' },
        { name: '财政局', unit: '次', value: 56, typeName: '' },
        { name: '林业局', unit: '次', value: 5, typeName: '' },
        { name: '城管局', unit: '次', value: 66, typeName: '' },
        { name: '发改委', unit: '次', value: 8, typeName: '' },
        { name: '工信局', unit: '次', value: 2, typeName: '' },
        { name: '教体局', unit: '次', value: 4567, typeName: '' },
        { name: '科技局', unit: '次', value: 33, typeName: '' },
        { name: '农村农业局', unit: '次', value: 6, typeName: '' },
        { name: '卫健委', unit: '次', value: 878, typeName: '' },
        { name: '广旅广电局', unit: '次', value: 56, typeName: '' },
        { name: '税务局', unit: '次', value: 14, typeName: '' },
        { name: '税务局', unit: '次', value: 14, typeName: '' }
      ],
      ChartOption: null,
      ChartOption2: null,
      totalText: '',
      totalTextUnit: '',
      offsetData: [],
      symbolSizeData: [],
      echartsColor: bgColor,
      // 图表容器宽度
      boxWidth: 820,
      chartsWidth: 410,
      // 滚动定时器
      moveInterval: null
    };
  },
  watch: {
    // options: {
    //   handler(nvalue, ovalue) {
    //   },
    //   deep: true,
    //   immediate: true,
    // },
    // selectXzqdm: {
    //   handler(nvalue, ovalue) {
    //     this.getData();
    //   },
    //   deep: true,
    //   immediate: true,
    // },
  },
  mounted() {
    this.getData();
    let that = this;
    window.addEventListener('resize', function () {
      that.getData();
    });
    this.moveCharts();
  },
  beforeDestroy() {
    window.removeEventListener('resize', function () { });
  },
  methods: {
    // 设置坐标点和颜色
    getSymbolSizeData() {
      if (!this.echartsData.length) return;
      let top = Math.floor((this.echartsData.length - 1) / 2);
      this.echartsData.filter((item, index) => {
        if (index <= top) this.symbolSizeData.push([index * 25, 75]);
        if (index > top) this.symbolSizeData.push([(index - top - 1) * 25, 26]);
      });
      for (let i = 0; i < Math.floor(this.echartsData.length / 16); i++) {
        this.echartsColor = [...this.echartsColor, ...bgColor];
      }
    },
    getData() {
      // let jsonFilter = ' 1=1 ';
      // if(this.selectXzqdm){
      //   jsonFilter = getFilterBySql(`\"xzqdm\" = \'${this.selectXzqdm}\'`);
      // }
      // api.target.getdefaulttablebyfilter({ code: this.configData.targetcode, jsonfilter: jsonFilter, }).then((e) => {
      //   if (e.status != 200) {
      //     this.$Message.warning('获取数据失败');
      //     return;
      //   }
      //   this.echartsData = e.data;
      //   this.getEchart();
      // }).catch((e) => {
      //   this.$Message.warning('获取数据失败');
      // });
      if (this.echartsData.length > 14) {
        this.chartsWidth = Math.ceil((this.echartsData.length - 14) / 2) * 50 + 410;
        this.boxWidth = this.chartsWidth * 2;
      };
      this.getSymbolSizeData();
      this.getEchart();
    },
    getEchart() {
      let echartsData = this.echartsData;
      /*if (!isNull(this.selectXzqdm)) {
        echartsData = this.echartsData.filter(s =>
          s[this.configData.targetFilterField] === this.selectXzqdm
        );
      }*/
      this.totalText = echartsData.reduce((prev, cur, index, array) => {
        prev += cur.value;
        return prev;
      }, 0);
      this.totalTextUnit = echartsData.length > 0 ? echartsData[0].unit + echartsData[0].typeName : '';
      let args = {
        grid: {
          show: false,
          top: 0,
          bottom: 0,
          left: 25,
          right: 35
        },
        xAxis: [{
          gridIndex: 0,
          type: 'value',
          show: false,
          min: 0,
          nameLocation: 'middle',
          nameGap: 5,
        }],
        yAxis: [{
          gridIndex: 0,
          min: 0,
          show: false,
          max: 100,
          nameLocation: 'middle',
          nameGap: 30,
        }],
        series: [{
          type: 'scatter',
          symbol: 'circle',
          symbolSize: 100,
          label: {
            normal: {
              show: true,
              formatter: '{b}',
              color: '#fff',
              textStyle: {
                fontSize: '20',
              },
            },
          },
          itemStyle: {
            normal: {
              color: '#00acea',
            },
          },
          data: echartsData.map((s, index) => {
            return {
              name: s.name + '\n' + s.value + '\n' + s.unit + s.typeName,
              value: this.symbolSizeData[index],
              symbolSize: 46,
              label: {
                normal: {
                  textStyle: {
                    fontSize: 8,
                  },
                },
              },
              itemStyle: {
                normal: {
                  color: new echarts.graphic.RadialGradient(0.3, 0.5, 0.7, [{
                    offset: 0,
                    color: this.echartsColor[index][0],
                  },
                  {
                    offset: 1,
                    color: this.echartsColor[index][1],
                  }
                  ]),
                  opacity: 0.8,
                  shadowColor: '#045878',
                  shadowBlur: 10,
                  shadowOffsetX: 1,
                  shadowOffsetY: 1,
                },
              },
            };
          }),
        }],
      };

      if (!isNull(this.ChartOption)) {
        echarts.dispose(this.ChartOption);
      }
      this.$nextTick(() => {
        this.ChartOption = echarts.init(this.$refs.storeBar);
        this.ChartOption2 = echarts.init(this.$refs.storeBar2);
        this.ChartOption.setOption(args);
        this.ChartOption2.setOption(args);
      });
    },
    // 滚动
    moveCharts() {
      let that = this;
      let lefts = 0;
      this.moveInterval = setInterval(() => {
        lefts -= 1;
        that.$refs.box.style.left = lefts + 'px';
        if (lefts < -this.chartsWidth) lefts = 0;
      }, 40);
    },

  },
};
</script>

<template>
  <div class="SpatializationOfOperationalData">
    <div
      ref="box"
      :style="{width: boxWidth +'px'}"
      class="box"
    >
      <div
        :style="{width: chartsWidth +'px'}"
        class="service"
      >
        <div
          ref="storeBar"
          style="height:100%;width: 100%"
        >
        </div>
      </div>
      <div
        :style="{width: chartsWidth + 'px'}"
        class="service"
      >
        <div
          ref="storeBar2"
          style="height:100%;width: 100%"
        >
        </div>
      </div>
    </div>

  </div>
</template>

<style lang="less" scoped>
.SpatializationOfOperationalData {
  overflow: hidden;
  width: 100%;
  height: 100%;
  .box {
    position: relative;
    display: flex;
    height: 100%;
  }
  .service {
    height: 100%;
    position: relative;
    .storeBar {
      height: 100%;
      width: 100%;
    }
  }
}
</style>
